/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;
    
    .image{
        margin-top: $widget-image-margin-top;
    }
    .edgtf-blog-list-holder{
        
        .edgtf-post-title{
            line-height: $blog-list-widget-title-line;
        }
    
    }

    .edgtf-widget-title-holder {
        margin-top: $widget-title-margin-top;
        margin-bottom: $widget-title-margin-bottom;

        .edgtf-widget-title {
            color: $widget-heading-color;
            margin-bottom: 3px;
            font-size: $widget-title-font-size;
            font-weight: $widget-title-font-weight;
            letter-spacing: $widget-title-letter-spacing;
        }

        .edgtf-separator {
            border-bottom-width: 3px;
            border-color: $widget-text-color;
            width: 30px;
        }
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            display: block;
            line-height: $widget-list-item-height;
            //margin: 0 0 0 -8px;
            padding: $widget-list-item-padding-all;

	        &:first-child{
		        //padding-top: 2px;
	        }
            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            //border-top: 1px solid $widget-list-item-border-color;
            margin-top: $widget-list-item-padding;
            padding-left: $widget-inner-padding;
        }
    }

    .textwidget {
        a {
            color: $default-text-color;

            &:hover {
                color: $first-main-color-black;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and animation on link hover

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {
        .edgtf-widget-title {
            margin-bottom: 0;
        }

        ul {
            li {
                overflow: hidden;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;

                    @include edgtfTransition(padding .2s ease);

                   

                    &:hover {
                    

                    
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and w/o animation on link hover

    &.widget_recent_comments,
    &.widget_rss {
        .edgtf-widget-title {
            margin-bottom: 0;
        }
    }
    
    &.widget_edgtf_instagram_widget{
        ul {
            margin: $widget-instagram-margin;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // calendar

    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            background-color: $widget-list-item-background-color;
            color: $widget-text-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // rss

    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search


    .edgtf-searchform {
        .input-holder {
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            height: 45px;
            margin-top: 19px;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 15px;
                font-weight: inherit;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;
                height: 45px;

                @include edgtfTransition(color .2s ease-in-out);
                
                i{
                    font-size: 13px;
                }
            }

            input.search-field {
                padding: 0 0 0 14px;
                width: calc(100% - #{$widget-search-form-height});
                height: 45px;

                @include edgtfPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                background: $widget-list-item-background-color;
                color: $widget-search-button-color;
                cursor: pointer;
                font-size: 18px;
                width: $widget-search-form-height;

                @include edgtfTransition(color .2s ease-in-out);

                &:hover {
                    color: $widget-link-hover-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }

                &.edgtf-search-submit {
                    &:hover {
                        color: #000;
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // combobox

    &.widget_archive,
    &.widget_categories,
    &.widget_text {
        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-radius: 0;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-select-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include edgtfTransition(color .2s ease-in-out);

            option {
            }
        }
    }

    .widget_media_image{
        margin-bottom: $widget-image-margin-bottom;
     
    }
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tag cloud

    .tagcloud {
        margin-top: -1px;
        a {
            display: inline-block;
            font-family: $default-text-font;
            font-size: 13px !important;
            font-weight: 600;
            line-height: $widget-tag-cloud-height;
            margin: 0 13px 0px 0;
            padding: 0 .1em 0 0;
            height: 33px;
            text-transform: uppercase;
            color: $widget-tab-color;
            letter-spacing: 0.15em;

            &:hover {
                color: $widget-tag-hover;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // text

    &.widget_text {
        img {
            display: block;
        }

        p {
            &:first-child {
                margin-top: 0;
                padding-top: $widget-list-item-padding-top;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search
    &.widget_search{
        margin-top: -3px;
    }
}