.edgtf-side-menu {
    // widgets spacing
    $widget-title-margin-top: 0px;
    $widget-title-margin-bottom: 12px;
    $widget-bottom-margin: 9px;
    $widget-bottom-padding: 15px;
    $widget-inner-padding: 15px;
    $widget-image-margin-top: 7px;

    // widget color
    $widget-background-color: transparent;

    // widget border
    $widget-border-color: $default-border-color;
    $widget-border-width: 1px;
    $widget-border-style: solid;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 16px;
    $widget-list-item-padding-all: 0px 16px 16px 0px;
    $widget-list-item-padding-top: 4px;

    // list items color
    $widget-list-item-background-color: #999999;

    // list items border
    $widget-list-item-border-color: #cccccc;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: #333333;
    $widget-heading-color: #999999;
    $widget-title-font-size: 15px;
    $widget-title-font-weight: 600;
    $widget-title-letter-spacing: 0.2em;
    // links
    $widget-link-color: $default-text-color;
    $widget-link-hover-color: $first-main-color-black;

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 33px;
    $widget-search-button-color: $first-main-color;
	$widget-tab-background: #f7f7f7;
    $widget-tab-color: $default-text-color;
    $blog-list-widget-title-line: 1em;
	$widget-select-color: #929292;
    $widget-tag-hover: $first-main-color;
    $widget-tag-text-hover: #fff;
    $widget-image-margin-bottom: 10px;
    $widget-instagram-margin: 0;
    
    
    @import 'widgets/default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';
    @import 'widgets/social-icons-group-widget';
    @import 'widgets/blog-list-widget';
}