$timetable-background-color: #fff;
$timetable-border-color: $default-border-color;


.single-events{
    .tt_event_items_list {
        padding: 10px 0!important;
        li.type_info {
            border-bottom: 1px solid #e6e6e6!important;
            label {
                font-size: 15px!important;
                font-weight: 600!important;
                line-height: 25px!important;
                font-family: $default-text-font !important;
                color: $default-button-color !important;
                text-transform: uppercase!important;
            }
        }
    }
}

table.tt_timetable {
    font-weight: 400;
    color: #808080;
    font-size: 16px;
    border-collapse: collapse !important;
    border: 1px solid #e5e5e5;
    border-spacing: 0px!important;

    thead tr.row_gray {
        background-color: #fff !important;
        border: 1px solid $timetable-border-color;
        height: 80px;
    }

    tbody tr {
        border: none;
        border-top: 1px solid $timetable-border-color;
        border-bottom: 1px solid #e5e5e5;
        background-color: $timetable-background-color !important;
        &:last-child {
            border-bottom: none !important;
        }
    }

    td.tt_hours_column {
        font-size: 12px!important;
        font-weight: 500;
        line-height: 1.6em;
        letter-spacing: 0.2em;
        font-family: $default-text-font;
        color: $default-heading-color!important;
    }

    th {
        color: #fff;
        height: 70px;
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 400;
        line-height: 1.6em;
        letter-spacing: 2px;
        font-family: $default-text-font;
        box-sizing: border-box;
        padding: 14px 14px 12px 16px!important;
    }

    td {
        font-weight: 400;
        color: #808080;
        font-size: 12px;
        height: 120px;
        border: 1px solid #e5e5e5 !important;
        position: static;
        
    }

    .row_gray {
        background-color: $timetable-background-color !important; //important because plugin css is also using important
            th {
                background-color: $timetable-background-color !important; //important because plugin css is also using important
                color: $default-heading-color;
                font-size: 12px !important;
                font-weight: 500;
                letter-spacing: 0.2em;
                text-transform: uppercase;
                border: 1px solid #e5e5e5!important;
            }
    }

    .event_container {
        height: auto;
        box-sizing: border-box;
    }

    .event {
        background-color: transparent;
        color: #808080;
        vertical-align: middle;

        .event_header {
            font-size: 15px;
            line-height: 25px;
            font-weight: 600;
            color: $default-heading-color!important;
            margin-bottom: 6px;
        }

        .top_hour,
        .bottom_hour {

            display: none;

            .hours {
                color: #fff !important;
            }
        }

        .top_hour {

            text-align: right;
            padding-right: 5px;
            position: relative;

            &:after {
                position: absolute;
                content: "-";
                right: -2px;
                color: #fff;
            }
        }

        .bottom_hour {
            text-align: left;
            padding-left: 5px;
        }

        .before_hour_text,
        .after_hour_text {
            font-size: 14px;
            color: $default-text-color;
        }
        
        .after_hour_text {
            display: inline-block;
            margin-top: 6px;
            letter-spacing: 0.1em
        }

        &:not(.tt_single_event) {
            .event_container {
                &:last-child {
                    border-bottom: none;
                }
            }
        }


        hr {
            display: none;
        }

        &:hover, .event_container.tt_tooltip:hover {
            background-color: transparent;
        }

        &.tt_tooltip:hover .hours, .event_container.tt_tooltip:hover .hours {
            color: #808080;
        }

        a:hover, a.event_header:hover {
            text-decoration: none;
        }
    }

    .tt_tooltip_text {
        top: auto !important;
        left: 50% !important;
        bottom: calc(100% - 20px) !important;
        display: inline-block;
        height: auto !important;
        box-sizing: border-box;
        width: 133px;

        .tt_tooltip_content {
            color: #fff;
            background-color: $first-main-color-black;
            text-align: center;
            padding: 9px 18px 5px 18px;
            margin: 0;
            font-size: 16px;
            font-weight: 600;
            text-transform: uppercase;
            box-sizing: border-box;
            border-radius: 0;
            letter-spacing: 1px;

            a {
                display: none;

            }
        }

        .tt_tooltip_arrow {
            border-color: $first-main-color-black transparent;
            border-width: 8px 8px 0;
            bottom: 0;
            z-index: 2;
            display: block;
            height: 0;
            width: 0;
            left: 50%;
        }
    }

}


.tt_navigation_wrapper .tt_navigation_cell {
    margin: 0 auto !important;
}

.tt_tabs {
    text-align: center;

    .tt_tabs_navigation {
        display: inline-block;

        margin-bottom: 14px;

        li {

            margin: 0 5px 20px!important;
            float: none;
            display: inline-block;
        }

        li a {
            background-color: transparent;
            font-size: 15px !important;
            font-weight: 600;
            line-height: 1.6em;
            letter-spacing: 0.2em;
            font-family: $default-text-font;
            color: $default-heading-color !important;
            text-transform: uppercase;
            box-sizing: border-box;
            padding: 7px 28px !important;
            border: none !important;
            margin: 0;

            -webkit-transition: color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out !important;
            -moz-transition: color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out !important;
            transition: color 0.2s ease-out, background-color 0.2s ease-out, border-color 0.2s ease-out !important;
        }

        .ui-tabs-active a,
        li a:hover {
            color: $default-button-color !important;
        }
    }
}

#qodef-timetable-transparent {

    table.tt_timetable {
        .event {
