@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.edgtf-comment-holder {
    @include edgtfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .edgtf-comment-holder-inner {
        padding: 20px 0 79px;
    }

    .edgtf-comment {

        &.edgtf-pingback-comment {

            .edgtf-comment-text {
                padding: 0;
            }
        }
    }
    
    .edgtf-comments-number{
        margin-left: -6px;
    }

    .edgtf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include edgtfRelativeHolderLayout();
            margin: 0 0 0;
            padding: 62px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 146px;

	        li {

		        padding: 23px 0 0;
	        }
        }
    }

    .edgtf-comments-title {
        margin-bottom: 35px;

        > * {
            margin: 0;
        }
    }

    .edgtf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 104px;
        height: 104px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .edgtf-comment-text {
        padding: 0 0 0 146px;
        min-height: 82px;
        
        .edgtf-comment-edit-reply{
            display: table;
            margin-top: 10px;
            
            .edgtf-reply-link, .edgtf-edit-link{
                display: table-cell;
                &:hover{
                    a{
                        color: $default-heading-color;
                    }
                }
            }
    
            .edgtf-edit-link{
                vertical-align: bottom;
                padding-left: 20px;
                i{
                    font-weight: 600;
                }
            }
	        
	        .edgtf-comment-reply, .edgtf-comment-edit{
		        text-transform: uppercase;
		        font-weight: 600;
		        font-size: 13px;
	        }
        }

        .edgtf-comment-info {
            @include edgtfRelativeHolderLayout();
            margin: 6px 0 0;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: left;
            margin-right: 10px;
            font-size: 13px;
            color: #f66c3b;
            font-weight: 600;

            i{
                font-weight: 600;
            }
        }

        .edgtf-comment-name {
            float: left;
            margin: 0;
        }

        .edgtf-comment-date {
            float: left;
            margin-top: -4px;
            padding: 0 0 0 22px;
        }

        .edgtf-text-holder p {
            margin: 13px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color-black;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.edgtf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .edgtf-comment-form-inner {
        margin-top: -3px;
        padding: 0 0 38px;
    }

    .comment-reply-title {
        margin: 0 0 16px;
    }

    .logged-in-as {
        margin: 0 0 16px;
    }
    
    
    .edgtf-comment-form {
    
        .edgtf-comment-form-inner {
            padding: 37px 0 56px;
        }
    }
}

@include ipad-portrait {

    .edgtf-comment-holder {
       margin-top: 8px;

        .edgtf-comment-list {

            .children {
                padding: 0 0 0 40px;
                
                li{
                    margin-top: 24px;
                }
            }
        }
    }
}

@include phone-landscape {

    .edgtf-comment-holder {

        .edgtf-comment-list {

            .children {
                padding: 0;
                li{
                    margin-top: 14px;
                }
            }
        }

        .edgtf-comment-text {

            .edgtf-comment-date,
            .edgtf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
                font-weight: 600;
            }
        }
    }
}

@include phone-portrait {

    .edgtf-comment-holder {
        .edgtf-comment-list {
            li{
                padding: 0px 0 47px;
            }
        }
        .edgtf-comment-image {
            float: none;
        }

        .edgtf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
        .edgtf-comment-text {
            .edgtf-comment-date {
                padding: 4px 0 0 0;
            }
        }
        .edgtf-comment-edit-reply{
            margin-bottom: 9px;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.edgtf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
