    @extend %input-style;
    padding: 12px 15px;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;
        footer & {
            margin: 0;
        }
    
        &::placeholder{
            font-size: 15px!important;
            font-weight: 400;
            color: $default-text-color;
        }
    }
}

.edgtf-btn-icon-cf7{
    color: #fff;
    position: relative;
    right: 70px;
    top: 5px;
}

.your-name{
    .wpcf7-form-control{
        width: 48%;
        float: left;
        height: 44px;
    }
}

.your-email{
    .wpcf7-form-control{
        width: 48%;
        margin-left: 25px;
        height: 44px;
    }
}

.edgtf-cf-newsletter{
    margin-top: 30px;
}


.edgtf-cf-newsletter, .edgtf-cf-newsletter-footer, .edgtf-cf-newsletter-classic, .edgtf-cf-newsletter-coming-soon{
    text-align: center;
    margin-top: 28px;
    position: relative;
    .edgtf-column-right{
        position: absolute;
        top: 0;
        right: 0;
        display: block;
        width: 80px;
        padding-right: 5px;
    }
    
    .newsletter-submit input.wpcf7-form-control.wpcf7-submit {
        font-family: 'Font Awesome 5 Free';
        position: relative;
        line-height: 1em;
        font-size: 13px;
        padding: 12px 12px;
        color: $first-main-color;
        background:transparent;
        margin-top: 7px!important;
    }
    
    span {
        
        .wpcf7-form-control {
            padding-left: 37px;
            height: 53px;
            width: 37%;
            background-color: #fff;
            border-color: $default-text-color;
            margin-left: 0;
    
            &::placeholder {
                font-size: 15px !important;
                font-weight: 400;
                color: $default-text-color;
            }
            
            &:after{
                content: '\35';
                font-family: 'ElegantIcons';
                font-size: 18px;
                display: inline;
                position: absolute;
                right: 23px;
                color: #ef602c;
            }
        }
        
        &:after {
            //content: "\f124";
            font-family: 'Font Awesome 5 Free';
            
            font-size: 13px;
            line-height: inherit;
            font-weight: 600;
            
            display: inline;
            position: absolute;
            right: 23px;
            color: #ef602c;
            top: -3px;
        }
    
        .wpcf7-not-valid-tip{
            position: absolute;
            font-size: 13px;
            &:after{
                display: none;
            }
        }
    }
    .wpcf7-submit{
        margin: -4px 0 0 38px!important;
        padding: 12px 46px 12px 32px;
        
    }
    
}

.edgtf-cf-newsletter-classic, .edgtf-cf-newsletter-coming-soon{
    .newsletter-submit input.wpcf7-form-control.wpcf7-submit {
    
    }
}

.edgtf-cf-newsletter-footer, .edgtf-cf-newsletter-classic, .edgtf-cf-newsletter-coming-soon{
    span {
        .wpcf7-form-control {
            width: 100%;
            padding-left: 20px;
           
        }
    }
